import React from 'react'
import CloseImage from '../../../assets/close.png'

const RoundHistory = (props) => {
    const { roundModalClose, historyArray, playTime, answer, scorePoint, nickname } = props

    return (
        <div>
            <div>
                <div className="front-ground">
                    <div className="modal">
                        <div className='modal-header'>
                            <h1>{nickname}  </h1>
                            <button onClick={roundModalClose}><img src={CloseImage} alt=" " className='img-close'/></button>
                        </div>
                        <div className='history-info'>
                            <div>
                                <div> : {playTime}</div>
                                <div> : {answer}</div>
                                <div> : {scorePoint}</div>
                            </div>    
                        </div>
                        <div className='history-head flex-row'>
                            <div className='his-head-trial'></div>
                            <div className='his-head-guess'></div>
                            <div className='his-head-strike'></div>
                            <div className='his-head-ball'></div>
                        </div>
                        {historyArray.map((history, index) => 
                        <div className='his-map-line' key={index}>
                            <span className='his-map-trial'>{index + 1}</span>
                            <span className='his-map-guess'>{history.guess}</span>
                            <span className='his-map-strike'>{history.strike}</span>
                            <span className='his-map-ball'>{history.ball}</span>
                        </div>)}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default RoundHistory